export const TextArea = ({input, meta, ...props})=> {
    return (
        <div className ={`${s.formControl} ${(meta.error && meta.touched) && s.error}`}>
            <textarea {...props} {...input} />
            <span>{meta.error}</span>
        </div>
    )
}

export const Input = ({input, meta, ...props})=> {
    return (
        <div className ={`${s.formControl} ${(meta.error && meta.touched) && s.error}`}>
            <input {...props} {...input} />
            <span>{meta.error}</span>
        </div>
    )
}

export const CustomCheckBox = props => {
    const hasError = props.meta.touched && props.meta.error;
    return (
        <div>
            {
                (props.type !== 'checkbox' && <props.type {...props.input} placeholder={props.placeholder} />)
                || <input type="checkbox"/>
            }
            {hasError && <span >{props.meta.error}</span>}
        </div>
    )
}

export const createFormField = (placeholder, name, validators, component, props={}, text="") => (
    <div>
        <Field placeholder={placeholder} name={name} component={component} validate={validators} {...props} />
        {text}
    </div>
);

